  background-color: #1c1c1c;
  margin: 0 auto;
  font-family: Quicksand, sans-serif;
  line-height: 1.5;
  position: relative;
  overflow: hidden;
}

p > a {
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 20px;
  left: 20px;
}

p > a:hover {
  color: #e6dbae;
}

