  padding: 1.25rem 2.5rem;
  font-family: Inter, sans-serif;
  font-size: 2rem;
  transition: transform .2s, box-shadow .2s;
  position: absolute;
  left: 50%;
}

button[data-start]:hover, button[data-stop]:hover {
  --border: hsl(var(--hue, 0), 80%, 50%);
